<template>
  <div class="search-detail">
    <!-- <div class="detail-header">
      <el-page-header @back="goBack" content="详情"></el-page-header>
    </div> -->
    <div class="detail-main">
      <div class="detail-title" v-html="lawObj.title" v-if="lawObj !== undefined"></div>
      <div class="detail-content" v-html="lawObj.content" v-if="lawObj !== undefined">
        
      </div>
    </div>
  </div>
</template>

/* eslint-disable no-irregular-whitespace */
<script>
export default {
  name: "SearchIndex",
  components: {},
  data() {
    return {
      id: this.$route.query.id,
      point: this.$route.query.point,
      keyword: this.$route.query.keyword,
      lawObj: undefined
    }
  },
  created() {
    // console.log('>>>>>>>', this.$route.query)
  },
  mounted() {
    this.loadLaw()
  },
  methods: {
    loadLaw() {
      if (this.id) {
      this.$axios({
        method: 'post',
        url: '/api/law/getLawDetail',
        data: {
          id: this.id
        }
      }).then(res => {
        console.log('>>>>>>>>>>>', res)
        const resData = res.data
        const tempObj = {}
        tempObj.title = resData.title
        let tempHtml = ''
        for (const m in resData.content) {
          let point = String(m)
          if (resData.content[m].partTitle) {
            tempHtml += `<h2 id="${point}">${resData.content[m].partTitle}  ${resData.content[m].partDes}</h2>`
          }
          for (const n in resData.content[m].partContent) {
            let pPoint = point + '-' + String(n)
            if (resData.content[m].partContent[n].chapterTitle) {
              tempHtml += `<h3 id="${pPoint}">${resData.content[m].partContent[n].chapterTitle}  ${resData.content[m].partContent[n].chapterDes}</h3>`
            }
            for (const j in resData.content[m].partContent[n].chapterContent) {
              let cPoint = pPoint + '-' + String(j)
              tempHtml += `<p id="${cPoint}"><b>${resData.content[m].partContent[n].chapterContent[j].articleTitle}</b>${resData.content[m].partContent[n].chapterContent[j].articleContent}</p>`
            }
          }
        }
        tempObj.content = tempHtml
        this.lawObj = tempObj

        this.$nextTick(() => {
          document.getElementById(this.point).scrollIntoView(true)
        })
      }).catch(err => {
        console.log(err)
      })
      } else {
        alert('页面访问异常，请关闭后重试！')
      }
    },
    goBack() {
      console.log("goBack");
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss">
.search-detail {
  margin: 0 auto;
  background-color: #e5f1ff;
  position: absolute;
  width: 100%;
  min-height: 100%;
  left: 0;
  top: 0;
  .detail-header {
    padding: 15px 20px;
    background-color: #ffffff;
  }
  .detail-main {
    .detail-title {
      padding: 20px 10px;
      line-height: 30px;
      background-color: #3460ab;
      overflow: hidden;
      text-align: center;
      overflow: hidden;
      color: #ffffff;
      font-size: 18px;
    }
    .detail-content {
      padding: 5px 15px;
      font-size: 12px;
      line-height: 24px;
      h2 {
        font-size: 16px;
        font-weight: bold;
        text-align: center;
        margin: 10px 0;
      }
      h3 {
        font-size: 16px;
        font-weight: normal;
        text-align: center;
        margin-bottom: 10px;
      }
      p {
        margin-bottom: 6px;
        b {
          margin-right: 6px;
        }
      }
    }
  }
}
</style>
